import React from 'react';
import EnhanceVerify from '../EnhanceVerify'
const Time = ({ onChange, name, value, title, placeholder, extype }) => {
    value = moment(value).format('YYYY-MM-DDTH:mm');
    if (value.length == 15) {
        value = value.replace('T', 'T0')
    }
    return (
        <div>
            {title && <div className='weui-cells__title'>{title}</div>}
            <div className="weui-cells weui-cells_form">
                <div className="weui-cell">
                    <div className="weui-cell__bd">
                        <input className="weui-input" type={extype || "datetime-local"} value={value} placeholder={placeholder} onChange={(e) => {
                            onChange && onChange(name, e.target.value)
                        }} />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default EnhanceVerify()(Time)


