
import React from 'react';
import modalbox from './Modalbox.scss';

class ModalBox extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            show: false
        }
    }
    _show() {
        this.setState({ show: true })
    }
    _hide() {
        this.setState({ show: false })
    }
    render() {
        let { title, value, list, children, type } = this.props
        let curtmp = ''
        if (type == 'checkbox') {
            curtmp = value && value.length > 0 ? '已选择' + value.length + '项' : '暂未选择'
        } else if (type == 'radio') {
            let cur = list.filter(function (d) {
                return value == d.id
            })
            curtmp = cur.length > 0 ? cur[0].text : '暂未选择'
        }
        let { show } = this.state
        let modalcontent = show ? modalbox.modalcontent : modalbox.modalcontenthide
        return (
            <div className={modalbox.modalbox}>
                <div className={modalbox.modalheader}>
                    <div className="weui-cells weui-cells_form">
                        <div className="weui-cell weui-cell_switch" onClick={this._show.bind(this)}>
                            <div className="weui-cell__bd">{title}</div>
                            <div className="weui-cell__ft">
                                <div className={modalbox.cur}>{curtmp}</div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className={modalcontent}>
                    <div className="warper">
                        <div className="header">
                            <a className="span"></a>
                            <div className="h1">{title}</div>
                            <a className="span icontitle" onClick={this._hide.bind(this)} >保存</a>
                        </div>
                        <div className="content">
                            {children}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default ModalBox


