import React from 'react';
const EnhanceVerify = (title='123') => (Component) => class extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            tip: null
        }
    }
    shouldComponentUpdate(nextProps, nextState) {
        if (nextProps.value == this.props.value) {
            return false
        }
        return true
    }
    render() {
        // console.log('更新：', this.props.name, '->', this.props.value);
        return <Component {...this.props} tip={this.state.tip} />;
    }
};

export default EnhanceVerify


