import { Component } from 'react'
import EnhanceVerify from '../EnhanceVerify'
class CheckBox extends Component {
    constructor(props) {
        super(props)
        this.state = {
            value: this.props.value || []
        }
        this.onChange = this.onChange.bind(this)
    }
    shouldComponentUpdate(nextProps, nextState) {
        if (nextProps.list != this.props.list || nextProps.value != this.props.value) {
            return true
        }
        return false
    }
    onChange(e) {
        let value = this.state.value.concat()
        let v = e.target.value
        let check = e.target.checked
        if (check) {
            value.push(v)
        } else {
            let index = value.indexOf(v)
            value.splice(index, 1)
        }
        this.setState({ value })
        let { onChange, name } = this.props
        onChange && onChange(name, value)
    }
    render() {
        let { list, name, title, ext } = this.props
        let { value } = this.state
        return (
            <div>
                {title && <div className='weui-cells__title'>{title}(多选)</div>}
                <div className={ext ? 'weui-cells weui-cells_radio' : 'weui-cells weui-cells_checkbox'}>
                    {list.map((d, i) => (
                        <label className="weui-cell weui-check__label" key={i}>
                            {!ext && <div className="weui-cell__hd">
                                <input type="checkbox"
                                    defaultValue={String(d.id)}
                                    className="weui-check"
                                    name={name}
                                    checked={value.indexOf(String(d.id)) != -1}
                                    onChange={this.onChange}
                                />
                                <i className="weui-icon-checked"></i>
                            </div>}
                            <div className="weui-cell__bd">
                                <p>{d.text}</p>
                            </div>
                            {ext && <div className="weui-cell__ft">
                                <input type="checkbox"
                                    defaultValue={String(d.id)}
                                    className="weui-check"
                                    name={name}
                                    defaultChecked={value.indexOf(String(d.id)) != -1}
                                    onChange={this.onChange}
                                />
                                <i className="weui-icon-checked"></i>
                            </div>}
                        </label>
                    ))}
                </div>
            </div>
        )
    }
}

export default EnhanceVerify()(CheckBox)



