
import React from 'react';
import EnhanceVerify from '../EnhanceVerify'
const Switch = ({ onChange, name, value, title, placeholder, defaultValue = 0 }) => (
    <div>
        <div className="weui-cells weui-cells_form">
            <div className="weui-cell weui-cell_switch">
                <div className="weui-cell__bd">{title}</div>
                <div className="weui-cell__ft">
                    <label className="weui-switch-cp">
                        <input className="weui-switch-cp__input" name={name} value='1' checked={value === undefined ? defaultValue : value} type="checkbox" onChange={(e) => {
                            if (onChange) {
                                if (value === undefined) {
                                    value = defaultValue
                                }
                                value = value ? 0 : 1
                                onChange(name, value)
                            }
                        }} />
                        <div className="weui-switch-cp__box"></div>
                    </label>
                </div>
            </div>
        </div>
    </div>
)

export default EnhanceVerify()(Switch)


