import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Link, Redirect } from 'react-router-dom'

import Dialog from 'Components/dialog/dialog';
import toast from 'Components/toast/action';
import Form from 'Components/forms/Form';

const formlist = [{
    title: '用户名',
    name: 'username',
    type: 'text',
    placeholder: '请输入你的用户名'
}, {
    title: '密码',
    name: 'password',
    type: 'text',
    extype: 'password',
    placeholder: '请输入你的密码'
}]

class Login extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            formdata: {
                username: 'tianez',
            },
            show: false,
            message: '',
        }
    }
    hanleChange = (formdata) => {
        this.setState({ formdata })
    }
    hanlePostLogin = async (from) => {
        this.setState({
            show: true,
            message: '登录中...'
        })
        let res = await apicloud('user/login', {
            method: 'post',
            data: from
        })
        this.setState({
            show: false,
            message: ''
        })
        if (res.success) {
            localStorage.authorization = res.data.id
            localStorage.userId = res.data.userId
            this.props.dispatch('user/save', { ...res.data })
            toast('登录成功')
        }
    }
    render() {
        const { location, user } = this.props
        const from = location.from || { pathname: '/ucenter' }
        let { formdata, show, message } = this.state
        return (
            user && user.size > 0 ?
                <Redirect to={from} /> :
                <div className='warper'>
                    <div className="header">
                        <div className="h1">用户登录</div>
                    </div>
                    <div className="content">
                        <Form formdata={formdata} formlist={formlist} onChange={this.hanleChange}>
                            <div className="weui-btn-area">
                                <input type='button' onClick={() => this.hanlePostLogin(this.state.formdata)} className="weui-btn weui-btn_primary" value='确定' />
                            </div>
                            <div className="weui-btn-area">
                                <Link to={'/register'} className="weui-btn weui-btn_primary">注册账号</Link>
                            </div>
                        </Form>
                    </div>
                    <Dialog show={show}>
                        <div className="weui-toast">
                            <i className="weui-loading weui-icon_toast"></i>
                            <p className="weui-toast__content">{message}</p>
                        </div>
                    </Dialog>
                </div>
        )
    }
}

export default connect((state) => ({ user: state.get('user') }))(Login)


