import React from 'react';
import EnhanceVerify from '../EnhanceVerify'

const Textare = ({ onChange, name, value, title, placeholder }) => (
    <div>
        {title && <div className='weui-cells__title'>{title}</div>}
        <div className="weui-cells weui-cells_form">
            <div className="weui-cell">
                <div className="weui-cell__bd">
                    <textarea className="weui-textarea"
                        value={value}
                        placeholder={placeholder}
                        onChange={(e) => {
                            if (onChange) {
                                onChange(name, e.target.value)
                            }
                        }}
                        rows="3"></textarea>
                    <div className="weui-textarea-counter"><span>{value && value.length || 0}</span></div>
                </div>
            </div>
        </div>
    </div>
)

export default EnhanceVerify()(Textare)


