import React from 'react';
import EnhanceVerify from '../EnhanceVerify'

const Input = ({ onChange, name, value = '', tip, title, placeholder, extype }) => (
    <div>
        {title && <div className='weui-cells__title'>{title}</div>}
        <div className="weui-cells weui-cells_form">
            <div className="weui-cell">
                <div className="weui-cell__bd">
                    <input className="weui-input" type={extype || "text"} value={value} placeholder={placeholder} onChange={(e) => {
                        onChange && onChange(name, e.target.value.trim())
                    }} />
                </div>
            </div>
        </div>
        {tip && <div className="weui-cells__tips">{tip}</div>}
    </div>
)

export default EnhanceVerify('okkk2')(Input)


