
import React from 'react';

import { Radio, CheckBox, Textare, Input, Switch, Time, Slider } from 'Components/forms';
import style from './Form.scss';
import ModalBox from './ModalBox/ModalBox'

class Form extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            formdata: {
                username: 'tianez22'
            }
        }
        this._onChange = this._onChange.bind(this)
    }

    _onChange(name, value) {
        if (this.props.onChange) {
            let { formdata = {} } = this.props
            formdata[name] = value
            this.props.onChange(formdata)
        } else {
            let { formdata = {} } = this.state
            formdata[name] = value
            this.setState({ formdata })
        }
    }
    render() {
        let { formlist, onChange, children } = this.props
        let { formdata = {} } = onChange ? this.props : this.state
        let _onChange = this._onChange
        return (
            <div className={style.form}>
                {formlist && formlist.map(function (d, i) {
                    d.onChange = _onChange
                    d.value = formdata[d.name]
                    d.key = i
                    switch (d.type) {
                        case 'text':
                            return <Input {...d} />
                            break;
                        case 'time':
                            return <Time {...d} />
                            break;
                        case 'textare':
                            return <Textare {...d} />
                            break;
                        case 'switch':
                            return <Switch {...d} />
                            break;
                        case 'radio':
                            return d.modal ? <ModalBox {...d}><Radio {...d} /></ModalBox> : <Radio {...d} />
                            break;
                        case 'checkbox':
                            return d.modal ? <ModalBox {...d}><CheckBox {...d} /></ModalBox> : <CheckBox {...d} />
                            break;
                    }
                })}
                {children}
            </div>
        )
    }
}

export default Form


