import React from 'react';
import EnhanceVerify from '../EnhanceVerify'

const Radio = ({ list, onChange, name, value, title, defaultValue = 0 }) => (
    <div>
        {title && <div className='weui-cells__title'>{title}(单选)</div>}
        <div className="weui-cells weui-cells_checkbox" id="form">
            {list.map((d, i) => {
                return (
                    <label className="weui-cell weui-check__label" key={i} >
                        <div className="weui-cell__hd">
                            <input type="radio"
                                defaultValue={d.id}
                                className="weui-check"
                                name={name}
                                checked={value ? d.id == value : d.id == defaultValue}
                                onChange={(e) => {
                                    onChange && onChange(name, e.target.value)
                                }}
                            />
                            <i className="weui-icon-checked"></i>
                        </div>
                        <div className="weui-cell__bd">
                            <p>{d.text}</p>
                        </div>
                    </label>
                )
            })}
        </div>
    </div>
)
export default EnhanceVerify()(Radio) 


